@use 'variables';
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  color: variables.$textColor;
  list-style: none;
}

__body {
  font-family: 'Arial', sans-serif;
  background-color: variables.$textColor;
  background-image: url('../images/aniver.jpg');
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: 'poppins', sans-serif;
}

__decorations img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
  width: 100%;
}

__container {
  max-width: 800px;
  margin: 50px auto;
  background-color: variables.$secondaryTextColor;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px variables.$numeberTextColor;
}

__countdown {
  text-align: center;
  margin-bottom: 20px;
}

__timer {
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
}

__decorations img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

__container __box {
  position: relative;
  height: 400px;
  background: variables.$numeberTextColor;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid variables.$secondaryTextColor;

  &::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: variables.$textColor;
    z-index: -1;
    transform: skew(2deg, 2deg);
  }
}

__container __box:hover {
  .__content h2 {
    top: -140px;
  }

  .__content a {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}

__content {
  position: relative;
  padding: 20px;
  transform: translateY(40px);

  h2 {
    position: absolute;
    top: -60px;
    right: 20px;
    margin: 0;
    padding: 0;
    font-size: 10em;
    color: variables.$numeberTextColor;
    transition: 0.5s;
  }

  h3 {
    margin: 0 0 10px;
    padding: 0;
    font-size: 24px;
    font-weight: 500;
    color: variables.$textColor;
  }

  p {
    margin: 0;
    padding: 0;
    color: variables.$textColor;
    font-size: 16px;
  }

  a {
    position: relative;
    margin: 20px 0 0;
    padding: 10px 20px;
    text-decoration: none;
    border: 1px solid variables.$textColor;
    display: inline-block;
    color: variables.$textColor;
    transition: .5s;
    transform: translateY(-40px);
    opacity: 0;
    visibility: hidden;

    &:hover {
      color: variables.$secondaryTextColor;
      background: variables.$textColor;
    }
  }
}
